@font-face {
  font-family: Akrobat;
  src: url(../../static/media/Akrobat-SemiBold.780f43e1.otf);
}

html {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: Akrobat;
  width: 100%;
  height: 100%
}

#root {
  width: 100%;
  height: 100%;
}

:focus {
  outline: none;
}
.startScreen-imageBackground {
  width: 100%;
  height: 100%;
  background-image: url(../../static/media/background-image.f98a94e3.jpg);
  background-size: cover;
  -webkit-animation: moveIt 600s linear infinite;
          animation: moveIt 600s linear infinite;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.startScreen {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.35);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #F1F1F1;
  font-weight: bold;
  font-size: 24px;
}

.startScreen-menu, .startScreen-selectName, .startScreen-createPrivateRoom {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 16px;
}

.startScreen h1 {
  font-size: 60px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  color: white;
}

.startScreen span {
  cursor: pointer;
  -webkit-transition: 0.4s all;
  transition: 0.4s all;
  text-align: center;
}

.startScreen span:hover {
  color: white;
}

.startScreen form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 8px;
}

.startScreen input[type="text"] {
  width: 100%;
  max-width: 400px;
  background-color: transparent;
  height: 40px;
  border: none;
  border-bottom: 2px solid #F1F1F1;
  font-size: 25px;
  padding-left: 8px;
  color: #F1F1F1;
  font-family: Akrobat;
  font-weight: bold;
  margin-bottom: 8px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.startScreen input[type="text"]::-webkit-input-placeholder {
  color: #F1F1F1;
}

.startScreen input[type="text"]::-ms-input-placeholder {
  color: #F1F1F1;
}

.startScreen input[type="text"]::placeholder {
  color: #F1F1F1;
}

.startScreen input[type="text"]:focus {
  border-bottom: 2px solid white;
  color: white;
  outline: none;
}

.startScreen-checkboxContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.startScreen input[type="checkbox"] {
  width: 20px;
  height: 20px;
}

.startScreen-checkboxContainer span {
  margin-left: 5px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.startScreen-checkboxChecked {
  color: white;
}

.startScreen button {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 40px;
  background-color: transparent;
  border: none;
  font-size: 25px;
  font-family: Akrobat;
  color: #F1F1F1;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  margin-top: 8px;
}

.startScreen button:hover {
  color: white;
}

@-webkit-keyframes moveIt {
  from {background-position: center left;}
  to {background-position: center right;}
}

@keyframes moveIt {
  from {background-position: center left;}
  to {background-position: center right;}
}
.game {
  width: calc(100% - 16px);
  height: calc(100% - 16px);
  padding: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.game-waiting {
  font-weight: bold;
  font-size: 18px;
}

.game-question {
  display: flex;
  flex-direction: column;
  flex-direction: center;
  justify-content: center;
  align-items: center;
  width: 90%;
  max-width: 500px;
}

.game-anotherPlayer, .game-player {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.game-anotherPlayer {
  top: 8px;
  left: 8px;
}

.game-player {
  top: 8px;
  right: 8px;
}

.game-anotherPlayerName, .game-playerYou {
  font-weight: bold;
  font-size: 18px;	
}

.game-question h2 {
  text-align: center;
}

.game-question button {
  width: 100%;
  max-width: 400px;
  margin: 4px;
  color: gray;
  font-family: Akrobat;
  font-weight: bold;
  font-size: 18px;
  background-color: transparent;
  border: none;
  padding: 8px;
  border-radius: 3px;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.2);
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.answerSelected {
  color: white !important;
  background-color: #2196F3 !important;
}

.correctAnswer {
  color: white !important;
  background-color: green !important;
}

.oponentAnswered {
  position: absolute;
  color: green;
  bottom: 8px;
}

.game-ended {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.game-ended h1 {
  margin: 4px;
  margin-bottom: 0px;
}

.game-ended h2 {
  margin: 0px;
  margin-bottom: 8px;
}

.game-ended button {
  width: 100%;
  max-width: 400px;
  margin: 4px;
  color: gray;
  font-family: Akrobat;
  font-weight: bold;
  font-size: 18px;
  background-color: transparent;
  border: none;
  padding: 8px;
  border-radius: 3px;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.2);
  -webkit-transition: all 0.5s;
  transition: all 0.5s;  
}
